import React from "react";
import tw from "twin.macro";
import styled from "styled-components";
import {useHistory} from "react-router-dom";

import {SectionHeading, Subheading as SubheadingBase} from "components/misc/Headings.js";
import {SectionDescription} from "components/misc/Typography.js";
import {PrimaryButton as PrimaryButtonBase} from "components/misc/Buttons.js";
import {Container as ContainerBase, ContentWithPaddingXl as ContentBase} from "components/misc/Layouts.js";

import {ReactComponent as Loader} from "images/svg-loaders/oval.svg";
import {ReactComponent as MiniLoader} from "images/svg-loaders/oval_mni.svg";
import {APP_BASE_URL} from "config";

const LoaderContainer = tw.div`max-w-screen-xl lg:w-1/2 xl:w-5/12 p-6 sm:p-12 flex justify-center items-center`;
const Container = tw(ContainerBase)`bg-primary-900 text-gray-100 -mx-8 px-8`;
const ContentWithPaddingXl = tw(
  ContentBase
)`relative z-10 mx-auto px-0 py-10 sm:px-6 md:px-8 lg:px-12 xl:px-24 sm:py-20 flex flex-col max-w-screen-xl`;
const HeaderContainer = tw.div`mt-10 w-full flex flex-col items-center`;
const Subheading = tw(SubheadingBase)`mb-4 text-gray-100`;
const Heading = tw(SectionHeading)`w-full`;
const Description = tw(SectionDescription)`w-full text-gray-300 text-center`;

const PlansContainer = tw.div`mt-16 flex flex-col items-center lg:flex-row lg:items-stretch lg:justify-between text-gray-900 font-medium`;
const PlanRemainingContainer = tw.div`mt-16 flex flex-col lg:flex-row lg:items-stretch text-gray-900 font-medium`;
const Plan = styled.div`
  ${tw`md:w-1/2 lg:w-1/3 bg-white rounded-lg shadow-sm py-10 px-6 sm:px-10 lg:px-6 lg:py-10 xl:p-10 mx-3 flex flex-col justify-between mt-16 first:mt-0 lg:mt-0 shadow-raised`}
`;

const PlanHeader = styled.div`
  .nameAndFeaturedContainer {
    ${tw`flex flex-wrap flex-col sm:flex-row justify-between items-center`}
  }

  .name {
    ${tw`lg:text-lg xl:text-xl font-bold uppercase tracking-wider mr-3`}
  }

  .featuredText {
    ${tw`text-xs font-bold px-3 rounded py-2 uppercase bg-green-300 text-green-900 leading-none mt-4 sm:mt-0 w-full sm:w-auto text-center`}
  }

  .pricingContainer {
    ${tw`mt-6 flex items-end justify-between`}
    .currentPrice {
      ${tw`text-lg font-bold leading-none`}
      .bigText {
        ${tw`text-3xl font-bold`}
      }
    }

    .oldPrice {
      ${tw`text-gray-500 text-lg line-through hidden sm:block`}
    }
  }

  .description {
    ${tw`mt-8 font-medium text-gray-700 lg:text-sm xl:text-base`}
  }
`;
const PlanAction = tw.div`mt-4`;
const ActionButton = styled(PrimaryButtonBase)`
  ${tw`block text-center text-sm font-semibold tracking-wider w-full text-gray-100 bg-primary-500 px-6 py-4 rounded hover:bg-primary-700 focus:shadow-outline focus:outline-none transition-colors duration-300`}
`;

const WhiteBackgroundOverlay = tw.div`absolute inset-x-0 bottom-0 h-1/6 lg:h-1/3 bg-white z-0`;

export default ({
                  subheading = "",
                  heading = "Vrification de votre paiement",
                  description = "Slectionner votre ticket",
                  primaryButtonText = "Payer"
                }) => {

  const history = useHistory();

  const [isInitializing, setIsInitializing] = React.useState(true);

  const refAndRouterId = window.location.search.replace('?ref=', '');

  const [selectedPlan, setSelectedPlan] = React.useState(null);

  const [router, setRouter] = React.useState(null);

  const [error, setError] = React.useState(null);

  const [isPaying, setIsPaying] = React.useState(false);

  const [isPaid, setIsPaid] = React.useState(false);

  const [isPaidError, setIsPaidError] = React.useState(false);

  const [isPaidErrorMessage, setIsPaidErrorMessage] = React.useState('');

  const [isPaidErrorCode, setIsPaidErrorCode] = React.useState('');

  const [receiptsPrices, setReceiptsPrices] = React.useState(null);
  const [receiptsPricesRow, setReceiptsPricesRows] = React.useState([]);
  const [receiptsPricesRowRemaining, setReceiptsPricesRowsRemaining] = React.useState([]);

  React.useEffect(() => {
    try {
      if (!refAndRouterId) {
        history.push('/');
      } else if (receiptsPrices === null) {
        setIsInitializing(true);
        fetch(APP_BASE_URL + `/api/receipts/prices?ref=${refAndRouterId}`)
          .then(res => res.json())
          .then(res => {
            const prices = res['data']['prices'];
            const router = res['data']['router'];
            setReceiptsPrices(res['data']);
            setRouter(router);
            setIsInitializing(false);

            const length = prices.length;

            const remainingLength = length % 3;
            const rows = Math.floor(length / 3);
            const pricesRows = [];
            for (let i = 0; i < rows; i++) {
              pricesRows.push(prices.slice(i * 3, i * 3 + 3));
            }
            const pricesRowsWithRemaining = prices.slice(rows * 3, rows * 3 + remainingLength);

            setReceiptsPricesRows(pricesRows);
            setReceiptsPricesRowsRemaining(pricesRowsWithRemaining);

          })
          .catch(err => {
            setReceiptsPrices([]);
            setIsInitializing(false);
            setError(err);
            history.push('/');
          });
      }
    } catch (e) {
      console.error(e);
      setError(e);
    }
  }, [refAndRouterId]);

  const payViaCinetPay = (plan) => {

    setSelectedPlan(plan);
    setIsPaying(true);

    console.log(plan);

    return fetch(APP_BASE_URL + '/api/receipts/pay', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Access-Control-Allow-Origin': '*',
      },
      body: JSON.stringify({
        ref: refAndRouterId,
        price: plan.name,
      })
    })
      .then((response) => response.json())
      .then((response) => {
        if (response.success === true) {
          window.location.href = response['data'];
          //location.assign("http://www.mozilla.org"); // or
          // location = "http://www.mozilla.org";
        } else {
          setIsPaidError(true);
          setIsPaidErrorMessage(response.message);
          setIsPaidErrorCode(response.code);
          setIsPaying(false);
          setSelectedPlan(null);
        }
      })
      .catch((error) => {
        console.error(error);
        setIsPaying(false);
        setSelectedPlan(null);
      });

  };

  return (
    <Container>
      {<ContentWithPaddingXl>
        <HeaderContainer>
          {isInitializing ? <Loader/> : <></>}
          {isInitializing ? <></> : subheading && <Subheading>{subheading}</Subheading>}
          {isInitializing ? <></> : <Heading>{router === null ? heading : router.name}</Heading>}
          {isInitializing ? <Description style={{
            maxWidth: "500px",
            margin: "20vh",
          }}>{'Chargement des tickets disponibles ...'}</Description> : description &&
            <Description>{description}</Description>}
        </HeaderContainer>
        {isInitializing ? <LoaderContainer style={
            {
              height: '50vh',
              width: '50vh',
            }
          }/> :
          <React.Fragment>
            {
              receiptsPricesRow.map((rowPrices, index) => {
                return <PlansContainer key={index}>
                  {rowPrices.map((plan, index) => (
                    <Plan key={index} featured={plan.featured}>
                      <PlanHeader>
                <span className="nameAndFeaturedContainer">
                  <span className="name">{plan['session_time']}</span>
                  {plan.featured && <span className="featuredText">{plan.featured}</span>}
                </span>
                        <div className="pricingContainer">
                  <span className="currentPrice">
                    <span className="bigText">{plan.price}</span>
                    {"FCFA "}
                  </span>
                          <span className="currentPrice">
                    <span className="bigText">{plan.value}</span>
                  </span>
                        </div>
                        <p className="description">{'Valide pendant ' + plan.validity + ' jour(s)'}</p>
                      </PlanHeader>
                      <PlanAction>
                        {<ActionButton
                          style={{
                            backgroundColor: plan['available'] ? '' : '#8c8989',
                          }}
                          as="button" onClick={() => {
                          if (plan['available'] ?? true) {
                            payViaCinetPay(plan);
                          }
                        }
                        }>
                          {isPaying && plan.id === selectedPlan.id ? <div style={
                            {
                              width: '100%',
                              alignItems: 'center',
                              justifyContent: 'center',
                              display: 'flex',
                            }
                          }>
                            <MiniLoader/>
                          </div> : primaryButtonText}
                        </ActionButton>}
                      </PlanAction>
                    </Plan>
                  ))}
                </PlansContainer>
              })
            }
            <PlanRemainingContainer key={receiptsPricesRow.length}>
              {receiptsPricesRowRemaining.map((plan, index) => (
                <Plan key={index} featured={plan.featured}>
                  <PlanHeader>
                <span className="nameAndFeaturedContainer">
                  <span className="name">{plan.name}</span>
                  {plan.featured && <span className="featuredText">{plan.featured}</span>}
                </span>
                    <div className="pricingContainer">
                  <span className="currentPrice">
                    <span className="bigText">{plan.price}</span>
                    {"FCFA "}
                  </span>
                      <span className="currentPrice">
                    <span className="bigText">{plan.value}</span>
                  </span>
                    </div>
                    <p className="description">{'Valide pendant ' + plan.validity + ' jour(s)'}</p>
                  </PlanHeader>
                  <PlanAction>
                    {<ActionButton
                      style={{
                        backgroundColor: plan['available'] ? '' : '#8c8989',
                      }}
                      as="button" onClick={() => {
                      if (plan['available'] ?? true) {
                        payViaCinetPay(plan);
                      }
                    }
                    }>
                      {isPaying && plan.id === selectedPlan.id ? <div style={
                        {
                          width: '100%',
                          alignItems: 'center',
                          justifyContent: 'center',
                          display: 'flex',
                        }
                      }>
                        <MiniLoader/>
                      </div> : primaryButtonText}
                    </ActionButton>}
                  </PlanAction>
                </Plan>
              ))}
            </PlanRemainingContainer>
          </React.Fragment>}
      </ContentWithPaddingXl>}
      <WhiteBackgroundOverlay/>
    </Container>
  );
};
