import React from "react";
import tw from "twin.macro";
import {ReactComponent as EmailNewsletterIconBase} from "../../images/email-newsletter-icon.svg"
import {Container as ContainerBase} from "components/misc/Layouts.js"
import {SectionHeading} from "components/misc/Headings.js";
import {PrimaryButton} from "components/misc/Buttons.js";
import {ReactComponent as MiniLoader} from "images/svg-loaders/oval_mni.svg";
import {APP_BASE_URL} from "../../config";
import {
  notifyError,
  notifyLoading,
  notifyRemoveAll,
  notifyUpdateError,
  notifyUpdateSuccess
} from "../../config/notifications";
import {validateEmail} from "../../config/forms";

const Container = tw(ContainerBase)`bg-secondary-800 -mx-8`
const Content = tw.div`max-w-screen-xl mx-auto py-20 lg:py-24`;

const Row = tw.div`flex items-center justify-center flex-col lg:flex-row px-8`
const TextColumn = tw.div`flex items-center flex-col sm:flex-row`
const FormColumn = tw.div`mt-12 lg:mt-0 lg:ml-16 w-full sm:w-auto`

const EmailNewsletterIcon = tw(EmailNewsletterIconBase)`w-16 h-16 text-green-500`
const HeadingInfoContainer = tw.div`sm:ml-6 mt-6 sm:mt-0`
const Heading = tw(SectionHeading)`text-gray-100 sm:text-left leading-none`
const Description = tw.p`text-gray-500 font-medium text-sm max-w-sm mt-2 sm:mt-1 text-center sm:text-left`

const Form = tw.form`text-sm max-w-sm sm:max-w-none mx-auto`
const Input = tw.input`w-full sm:w-auto block sm:inline-block px-6 py-4 rounded bg-secondary-600 tracking-wider font-bold border border-secondary-600 focus:border-secondary-300 focus:outline-none sm:rounded-r-none hover:bg-secondary-500 transition duration-300 text-gray-200`
const Button = tw(PrimaryButton)`w-full sm:w-auto mt-6 sm:mt-0 sm:rounded-l-none py-4 bg-green-500 text-gray-100 hocus:bg-green-700 hocus:text-gray-300 border border-green-500 hocus:border-green-700`

export default () => {

  const [registeringEmail, setRegisteringEmail] = React.useState(false);

  const [email, setEmail] = React.useState('');

  const registerEmail = (e) => {
    e.preventDefault();
    try {
      notifyRemoveAll();

      if (!validateEmail(email)) {
        notifyError("Veillez entrez une adresse email valide.", {});
        return;
      }

      setRegisteringEmail(true);
      const notifyId = notifyLoading("Abonnement en cours...");

      fetch(APP_BASE_URL + '/api/newsletters/new', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Access-Control-Allow-Origin': '*',
        },
        body: JSON.stringify({
          email: email,
        })
      })
        .then((response) => response.json())
        .then((response) => {
          setRegisteringEmail(false);
          let message = response['message'];
          if (response.success === true) {
            setEmail('');
            if (message === undefined) {
              message = 'Abonnement  la newsletter russi.';
            }
            notifyUpdateSuccess(message, notifyId, {});
          } else {
            if (message === undefined) {
              message = "Une erreur s'est produite.\nRssayez plus tard.";
            }
            notifyUpdateError(message, notifyId, {});
          }
        })
        .catch((error) => {
          console.error(error);
          setRegisteringEmail(false);
        });
    } catch (e) {
      console.error(e);
      setRegisteringEmail(false);
    }
  }

  return (
    <Container>
      <Content>
        <Row>
          <TextColumn>
            <EmailNewsletterIcon/>
            <HeadingInfoContainer>
              <Heading>Newsletter</Heading>
              <Description>Abonnez-vous maintenant pour restez au courant de nos activits.</Description>
            </HeadingInfoContainer>
          </TextColumn>
          <FormColumn>
            <Form onSubmit={registerEmail}>
              <Input
                value={email}
                helperText={"Email invalide"}
                name="newsletter" type="email" placeholder="Votre adresse email" onChange={(e) => {
                setEmail(e.target.value);
              }}/>
              <Button onClick={(e) => registerEmail(e)} type="button"> {registeringEmail ?
                <MiniLoader width={15} height={15}/> : "S'abonner maintenant"}</Button>
            </Form>
          </FormColumn>
        </Row>
      </Content>
    </Container>
  );
};
