// noinspection DuplicatedCode

import React from "react";
import tw from "twin.macro";
import styled from "styled-components";
import { css } from "styled-components/macro"; //eslint-disable-line

import LogoImage from "../../images/logo.png";
import LogoWithText from "images/site/logo_with_text.png"
import { ReactComponent as FacebookIcon } from "../../images/facebook-icon.svg";
import { ReactComponent as TwitterIcon } from "../../images/twitter-icon.svg";
import { ReactComponent as YoutubeIcon } from "../../images/youtube-icon.svg";
import { ReactComponent as TikTok } from "../../images/tiktok-icon.svg";
import { ReactComponent as Instagram } from "../../images/instagram.svg";

const Container = tw.div`relative bg-gray-200 -mx-8 -mb-8 px-8`;
const Content = tw.div`max-w-screen-xl mx-auto pt-16 pb-8`
const FiveColumns = tw.div`max-w-screen-xl mx-auto py-16 lg:py-20 flex flex-wrap justify-between`;

const Column = tw.div`md:w-1/5`;
const WideColumn = tw(Column)`text-center md:text-left w-full md:w-2/5 mb-10 md:mb-0`;

const ColumnHeading = tw.h5`font-bold`;

const LinkList = tw.ul`mt-4 text-sm font-medium`;
const LinkListItem = tw.li`mt-3`;
const Link = tw.a`border-b-2 border-transparent hocus:text-primary-500 hocus:border-primary-500 pb-1 transition duration-300`;

const LogoContainer = tw.div`flex items-center justify-center md:justify-start`;
const LogoText = tw.h5`ml-2 text-xl font-black text-primary-500`;

const CompanyDescription = tw.p`mt-4 max-w-xs font-medium text-sm mx-auto md:mx-0 md:mr-4 `;

const SocialLinksContainer = tw.div`mt-4 `;
const SocialLink = styled.a`
  ${tw`cursor-pointer inline-block p-2 rounded-full bg-gray-700 text-gray-100 hover:bg-gray-900 transition duration-300 mr-4`}
  svg {
    ${tw`w-4 h-4`}
  }
`;

const CopyrightAndCompanyInfoRow = tw.div`pb-0 text-sm font-normal flex flex-col sm:flex-row justify-between items-center`;
const CopyrightNotice = tw.div``;
const CompanyInfo = tw.div``;

const Divider = tw.div`my-8 border-b-2 border-gray-800`

export default ({ sectionRef, menusRef }) => {

  const executeScroll = (ref) => ref?.current?.scrollIntoView()

  return (
    <Container ref={sectionRef}>
      <Content>
        <FiveColumns>
          <WideColumn>
            <LogoContainer>
              <img src={LogoWithText} width={192} alt="Wefree Logo"/>
            </LogoContainer>
            <CompanyDescription>
              Tl : (+228) 70 29 02 49
              <br/><br/>
              Ago ; Lom-TOGO.
              <br/><br/>
              E-mail : <a href="mailto:contact@mywefree.com">contact@mywefree.com</a>
              <br/><br/>
              Rejoignez-nous
            </CompanyDescription>
            <SocialLinksContainer>
              <SocialLink href="https://facebook.com/wefreetg">
                <FacebookIcon />
              </SocialLink>
              <SocialLink href="https://twitter.com/wefreetg">
                <TwitterIcon />
              </SocialLink>
              <SocialLink href="https://youtube.com">
                <YoutubeIcon />
              </SocialLink>
              <SocialLink href="https://www.instagram.com/wefreetg/">
                <Instagram />
              </SocialLink>
              <SocialLink href="https://www.tiktok.com/@wefreetg">
                <TikTok />
              </SocialLink>
            </SocialLinksContainer>
          </WideColumn>
          <Column>
            <ColumnHeading>Notre offre</ColumnHeading>
            <LinkList>
              <LinkListItem>
                <Link href="#!">Dcouvrir</Link>
              </LinkListItem>
              <LinkListItem>
                <Link href="#!">Acheter un ticket</Link>
              </LinkListItem>
              <LinkListItem>
                <Link href="#!">Tlchargez lapplication</Link>
              </LinkListItem>
            </LinkList>
          </Column>
          <Column>
            <ColumnHeading>Nos services</ColumnHeading>
            <LinkList>
              <LinkListItem>
                <Link href="#!">Services Wefree</Link>
              </LinkListItem>
              <LinkListItem>
                <Link href="#!">Je suis fournisseur de wifi zone</Link>
              </LinkListItem>
            </LinkList>
          </Column>
          <Column>
            <ColumnHeading>A propos</ColumnHeading>
            <LinkList>
              <LinkListItem>
                <Link href={'#!'} onClick={() => executeScroll(menusRef[0])}>Nos services</Link>
              </LinkListItem>
              <LinkListItem>
                <Link href={'#!'} onClick={() => executeScroll(menusRef[1])}>Notre quipe</Link>
              </LinkListItem>
              <LinkListItem>
                <Link href={'#!'} onClick={() => executeScroll(menusRef[2])}>Nos valeurs</Link>
              </LinkListItem>
              <LinkListItem>
                <Link href={'#!'} onClick={() => executeScroll(menusRef[3])}>Notre application</Link>
              </LinkListItem>
              <LinkListItem>
                <Link href={'#!'} onClick={() => executeScroll(menusRef[4])}>FAQs</Link>
              </LinkListItem>
              {/*<LinkListItem>
                <Link href="#!">Prsentation</Link>
              </LinkListItem>
              <LinkListItem>
                <Link href="#!">Notre mission</Link>
              </LinkListItem>
              <LinkListItem>
                <Link href="#!">Notre exprience</Link>
              </LinkListItem>
              <LinkListItem>
                <Link href="#!">Nos valeurs</Link>
              </LinkListItem>
              <LinkListItem>
                <Link href="#!">Nous contacter</Link>
              </LinkListItem>
              <LinkListItem>
                <Link href="#!">Politique de confidentialit</Link>
              </LinkListItem>*/}
            </LinkList>
          </Column>
        </FiveColumns>
        <Divider/>
        <CopyrightAndCompanyInfoRow>
          <CopyrightNotice>&copy; 2022 Copyright Wefree. Tous droits rservs</CopyrightNotice>
          <CompanyInfo>Dvelopp par WENDEL.</CompanyInfo>
        </CopyrightAndCompanyInfoRow>
      </Content>
    </Container>
  );
};
