import React from "react";
import styled from "styled-components";
import tw from "twin.macro";
//eslint-disable-next-line
import {css} from "styled-components/macro";
import {SectionHeading} from "components/misc/Headings.js";

import defaultCardImage from "../../images/shield-icon.svg";

import {ReactComponent as SvgDecoratorBlob3} from "../../images/svg-decorator-blob-3.svg";

import ShieldIconImage from "../../images/site/available.png";
import SupportIconImage from "../../images/site/innovation.png";
import CustomizeIconImage from "../../images/site/security.png";

const Container = tw.div`relative`;

const ThreeColumnContainer = styled.div`
  ${tw`flex flex-col items-center md:items-stretch md:flex-row flex-wrap md:justify-center max-w-screen-xl mx-auto py-10 md:py-12`}
`;
const Heading = tw(SectionHeading)`w-full`;

const Column = styled.div`
  ${tw`md:w-1/2 lg:w-1/3 px-6 flex`}
`;

const Card = styled.div`
  ${tw`flex flex-col mx-auto max-w-xs items-center px-6 py-5 rounded-lg mt-12`}
  .imageContainer {
    ${tw`border-2 border-primary-500 text-center rounded-full p-6 flex-shrink-0 relative`}
    img {
      ${tw`w-8 h-8`}
    }
  }

  .textContainer {
    ${tw`mt-6 text-center`}
  }

  .title {
    ${tw`mt-2 font-bold text-xl leading-none text-primary-500`}
  }

  .description {
    ${tw`mt-3 font-semibold text-secondary-100 text-sm leading-loose`}
  }
`;

const DecoratorBlob = styled(SvgDecoratorBlob3)`
  ${tw`pointer-events-none absolute right-0 bottom-0 w-64 opacity-25 transform translate-x-32 translate-y-48 `}
`;

export default ({ sectionRef }) => {
  /*
   * This componets has an array of object denoting the cards defined below. Each object in the cards array can have the key (Change it according to your need, you can also add more objects to have more cards in this feature component):
   *  1) imageSrc - the image shown at the top of the card
   *  2) title - the title of the card
   *  3) description - the description of the card
   *  If a key for a particular card is not provided, a default value is used
   */

  const cards = [{
    imageSrc: ShieldIconImage,
    title: "Qualit & Disponibilit",
    description: "Accdez  une interface conviviale, disponible 24h / 24, 7j / 7. Faites-vous assister en permanence par nos quipes."
  }, {
    imageSrc: SupportIconImage,
    title: "Simplicit & Innovation",
    description: "Achetez votre ticket wifi zone en ligne directement via T-money et Moov money en trs peu dactions. Gardez un historique de vos paiements. Achetez  distance des tickets pour vos proches."
  }, {
    imageSrc: CustomizeIconImage,
    title: "Intgrit & Scurit ",
    description: "Vos donnes et informations personnelles sont " +
      "confidentielles et scurises. Vos requtes et rclamations sont " +
      "traites en moins de deux (2) heures."
  }];

  return (<Container ref={sectionRef}>
    <ThreeColumnContainer>
      <Heading style={{
        fontSize: '2rem', textAlign: "center", color: 'black', fontWeight: 'normal'
      }} >Nos <span tw="text-primary-500">valeurs</span></Heading>
      {cards.map((card, i) => (<Column key={i}>
        <Card>
              <span className="imageContainer">
                <img width={135} height={135} src={card.imageSrc || defaultCardImage} alt=""/>
              </span>
          <span className="textContainer">
                <span className="title">{card.title || "Fully Secure"}</span>
                <p className="description" style={{ color: 'black' }}>
                  {card.description || "Lorem ipsum donor amet siti ceali ut enim ad minim veniam, quis nostrud. Sic Semper Tyrannis. Neoas Calie artel."}
                </p>
              </span>
        </Card>
      </Column>))}
    </ThreeColumnContainer>
    <DecoratorBlob/>
  </Container>);
};
