import toast from 'react-hot-toast';

export const notifySuccess = (message, {duration = 0, position = 'top-center', id = null}) => {
  return toast.success(message, {
    duration: duration,
    position: position,
    id: id,
  });
}

export const notifyError = (message, {duration = 0, position = 'top-center', id = null}) => {
  return toast.error(message, {
    duration: duration,
    position: position,
    id: id,
  });
}

export const notifyPromise = (promise, message, {
  onSuccess,
  onError,
  duration = 0,
  position = 'top-center',
  id = null
}) => {
  return toast.promise(promise, {
    loading: message,
    success: (data) => onSuccess(data),
    error: (err) => onError(err),
  }, {
    duration: duration,
    position: position,
    id: id,
  });
}

export const notify = (message, {duration = 0, position = 'top-center', id = null}) => {
  return toast(message, {
    duration: duration,
    position: position,
    id: id,
  });
}

export const notifyLoading = (message) => {
  return toast.loading(message);
}

export const notifyDismiss = (id) => {
  toast.dismiss(id);
}

export const notifyDismissAll = () => {
  toast.dismiss();
}

export const notifyRemove = (id) => {
  toast.remove(id);
}

export const notifyRemoveAll = () => {
  toast.remove();
}

export const notifyUpdateSuccess = (message, toastId, {position}) => {
  return notifySuccess(message, {
    position: position,
    id: toastId,
  });
}

export const notifyUpdateError = (message, toastId, {position}) => {
  return notifyError(message, {
    position: position,
    id: toastId,
  });
}