export const APP_PROD_BASE_URL = 'https://mywefree.com';
export const APP_DEV_BASE_URL = 'https://mywefree.com';
//export const APP_DEV_BASE_URL = 'http://localhost:8000';

export const APP_BASE_URL = isDevReact() ? APP_DEV_BASE_URL : APP_PROD_BASE_URL;
//export const API_BASE_URL = KYC_API_BASE_URL;

export function isDevReact() {
  try {
    // console.log('env:' + process.env.NODE_ENV);
    return !process.env.NODE_ENV || process.env.NODE_ENV === 'development';
  } catch (e) {
    // eslint-disable-next-line no-console
    console.debug(e);
    return false;
  }
}
