// noinspection DuplicatedCode

import React from "react";
import tw from "twin.macro";
import {SectionHeading, Subheading as SubheadingBase} from "components/misc/Headings.js";
import {
  Container as ContainerBase,
  ContentWithPaddingXl as ContentBase,
} from "components/misc/Layouts";
import {SectionDescription} from "components/misc/Typography";
import styled from "styled-components";
import {ReactComponent as Loader} from "../images/svg-loaders/bars.svg";
import {PrimaryButton as PrimaryButtonBase} from "../components/misc/Buttons";
import {APP_BASE_URL} from "../config";

import {
  notifyError,
  notifyLoading,
  notifyRemoveAll,
  notifyUpdateError,
  notifyUpdateSuccess
} from "../config/notifications";
import {Toaster} from "react-hot-toast";
import iconMain from "../images/icons/red_black/logo_transparent.png";

const Container = tw(ContainerBase)`bg-gray-100 text-gray-100 -mx-8 px-8 mb-20`;
const ContentWithPaddingXl = tw(
  ContentBase
)`relative z-10 mx-auto px-0 py-10 sm:px-6 md:px-8 lg:px-12 xl:px-24 sm:py-20 flex flex-col max-w-screen-xl`;
const HeadingContainer = tw.div`mt-10 w-full flex flex-col items-center`;
const Subheading = tw(SubheadingBase)`text-gray-100`;
const Heading = tw(SectionHeading)`w-full`;
const Description = tw(SectionDescription)`w-full text-gray-300 text-center`;

const PlansContainer = tw.div`mt-16 flex flex-col items-center lg:flex-row lg:items-stretch lg:justify-between text-gray-900 font-medium`;
const PlanRemainingContainer = tw.div`mt-16 flex flex-col lg:flex-row lg:items-stretch text-gray-900 font-medium`;
const Plan = styled.div`
  ${tw`md:w-1/2 lg:w-1/3 bg-white rounded-lg shadow-sm py-10 px-6 sm:px-10 lg:px-6 lg:py-10 xl:p-10 mx-3 flex flex-col justify-between mt-16 first:mt-0 lg:mt-0 shadow-raised`}
`;

const HostessContainer = tw.div`mt-3 flex flex-row sm:flex-row items-center justify-center flex-wrap max-w-screen-md mx-3`

const StatsContainer = tw.div`flex flex-col sm:flex-row items-center justify-center flex-wrap max-w-screen-md justify-between mx-auto`
const Stat = tw.div`flex flex-col text-center p-4 tracking-wide`
const StatKey = tw.div`text-xl font-medium`
const StatValue = tw.div`text-4xl sm:text-3xl md:text-4xl lg:text-5xl font-black`

const PrimaryButton = tw(PrimaryButtonBase)`mt-8 md:mt-10 text-sm inline-block mx-auto md:mx-0`;


export default () => {

  const [isInitializing, setIsInitializing] = React.useState(true);

  const transactionRef = window.location.search.replace('?ref=', '');

  const [message, setMessage] = React.useState(null);

  const [rewardingHostess, setRewardingHostess] = React.useState(false);

  const [hostessRewarded, setHostessRewarded] = React.useState(null);

  const [success, setIsSuccess] = React.useState(false);

  const [transaction, setTransaction] = React.useState(null);

  React.useEffect(() => {
    try {
      if (!transactionRef) {
        setMessage('Nous ne pouvons pas vrifier cette transaction.')
      } else if (transaction === null) {
        _verifyTransaction();
      }
    } catch (e) {
      console.error(e);
      setMessage('Une erreur s\'est produite.\nContactez votre fournisseur de point d\'accs.');
    }
  }, [transactionRef]);

  const _verifyTransaction = () => {
    setIsInitializing(true);
    fetch(APP_BASE_URL + `/api/wifi-zone/transaction/verify`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Access-Control-Allow-Origin': '*',
        },
        body: JSON.stringify({
          ref: transactionRef,
        })
      }
    )
      .then(res => res.json())
      .then(response => {
        setIsInitializing(false);
        if (response.success === true) {
          setMessage(response['data']['message']);
          setIsSuccess(response['data']['success']);
          setTransaction(response['data']);
        } else {
          setMessage(response['message']);
          setIsSuccess(false);
        }
      })
      .catch(_ => {
        setIsInitializing(false);
        setMessage('Une erreur s\'est produite.\nRessayez plus tard.');
      });
  }

  const rewardHostess = (hostess) => {

    notifyRemoveAll();
    setRewardingHostess(true);
    setHostessRewarded(hostess);

    const notifyId = notifyLoading("Patientez un instant...");

    fetch(APP_BASE_URL + '/api/wifi-zone/hostess/reward', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Access-Control-Allow-Origin': '*',
      },
      body: JSON.stringify({
        hostess: hostess['username'],
        router: transaction.router,
        provider: transaction.provider,
        ref: transactionRef,
      })
    })
      .then((response) => response.json())
      .then((response) => {
        setRewardingHostess(false);
        if (response.success === true) {
          notifyUpdateSuccess(response.message, notifyId, {});
          setTransaction({
            ...transaction,
            hostesses: [],
          });
        } else {
          notifyUpdateError(response.message, notifyId, {});
          setHostessRewarded(null);
        }
      })
      .catch((error) => {
        console.error(error);
        notifyUpdateError("Une erreur s'est produite.", notifyId, {});
        setRewardingHostess(false);
        setHostessRewarded(null);
      });

  };

  return (
    <Container>
      <ContentWithPaddingXl>
        <div style={{display: 'flex', alignContent: 'center', alignItems: 'flex-start'}}>
          <img src={iconMain} height={96} width={96} alt={"Icon Wefree"}/>
        </div>
        <HeadingContainer>
          <Heading style={{ color: 'black' }}>
            {isInitializing ? "Vrification du paiement ..." :
              ""
            }
          </Heading>
          {<Subheading style={{ textAlign: 'center', padding: '12px', fontSize: '18px', color: 'black', fontWeight: '600' }} >{isInitializing ? "Veillez patientez un instant." : message}</Subheading>}
          {isInitializing ? <Loader/> : <></>}
        </HeadingContainer>
        <br/>
        {isInitializing ? <></> :
          <>
            {(transaction?.['hostesses'] ?? []).length === 0 ? <></> :
              <>
                {<Subheading style={{
                  color: '#9E0E40',
                  textAlign: 'center',
                  fontWeight: '600',
                  fontSize: '21px'
                }}>Rcompenser un vendeur</Subheading>}
                <HostessContainer>
                  {(transaction?.['hostesses'] ?? []).map((hostess, index) =>
                    <Stat key={1}>
                      <PrimaryButton
                        onClick={() => {
                          rewardHostess(hostess);
                        }}
                        color={hostess['color_hex']}
                        style={{
                          borderColor: '#9E0E40',
                          borderWidth: '2px',
                          borderStyle: 'solid',
                          backgroundColor: hostess['color_hex'],
                          color: 'white',
                          width: '96px',
                          height: '96px',
                          borderRadius: '50%',
                        }}>
                      </PrimaryButton>
                    </Stat>
                  )}
                </HostessContainer>
              </>
            }
          </>
        }
        <br/>
        <br/>
        {success && transaction !== null && <Subheading style={{
          color: 'black',
          textAlign: 'center',
          fontWeight: '600',
          fontSize: '18px'
        }}>Pour vous connecter immdiatement</Subheading>}
        <StatsContainer>
          {
            isInitializing ? <></> :
              success && transaction !== null ?
                <>

                  <PrimaryButton
                    onClick={() => {
                      window.location.href = transaction['login_url'];
                      //window.location.replace(transaction['login_url']);

                      //location.assign("http://www.mozilla.org"); // or
                      // location = "http://www.mozilla.org";
                    }}
                    style={{
                      backgroundColor: '#229cb6',
                      margin: '10px',
                      fontWeight: '400',
                      fontSize: '18px'
                    }} as="button"
                    href={''}>{transaction['login_text']}
                  </PrimaryButton>
                </>
                 : <PrimaryButton
                onClick={_verifyTransaction}
                style={{
                  backgroundColor: '#044215',
                  margin: '10px',
                  fontWeight: '400',
                  fontSize: '18px'
                }} as="button"
                href={''}>{'Verifiez le paiement'}
              </PrimaryButton>
          }
        </StatsContainer>
      </ContentWithPaddingXl>
      <Toaster/>
    </Container>
  );
};
