import "tailwindcss/dist/base.css";
import "styles/globalStyles.css";
import React from "react";

import Home from "pages/Home.js";

import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import WifiZoneTickets from "./pages/WifiZoneTickets";
import WifiZonePayTransaction from "./pages/WifiZonePayTransaction";

export default function App() {
  return (
    <Router>
      <Switch>
        <Route path="/wifi-zone">
          <WifiZoneTickets />
        </Route>
        <Route path="/receipt/check">
          <WifiZonePayTransaction />
        </Route>
        <Route path="/">
          <Home />
        </Route>
      </Switch>
    </Router>
  );
}